import React from "react";


class Content extends React.Component {
    render() {
        return <div>
            <h3>
                   :
            </h3>
            <p> 2  2002 ,  ,   </p>
            <h3>: </h3>
            <p>  5, 2008-2016.</p>
            <p>  2, 2016-2019.</p>
            <p id="sevenEl" onMouseDown={this.ChangeColor}>
                   
                "  
                  ", 2019-2023.
            </p>
            <h3 id="nextEl" onMouseDown={this.ChangeColor}> </h3>
            <ul>
                <li></li>
                <li></li>
                <li> </li>
                <li> </li>
            </ul>
            <h3> </h3>
            <ol>
                <li>" "  </li>
                <li>"   ",  </li>
                <li>",   ",  </li>
            </ol>
            <h3></h3>
            <p> (. Nice, . Nizza, . Nicaea)            
                 .        .    ,
                     ,        .  
                      ,     
                 .         ,     ,
                     .
            </p>
        </div>;
    }

    ChangeColor(event){
        let el = event.target;
        if (el.classList.contains('SetColor')) {
            el.classList.remove('SetColor')
        } else{
            el.className = 'SetColor';
        }
    };
}

export default Content